import { PressEvent } from '@react-types/shared';
import { AnimatePresence, motion } from 'framer-motion';
import React, { RefAttributes, useMemo } from 'react';
import { useFocusRing, usePress } from 'react-aria';
import { transitions } from '../../../hooks/transitions';
import { clsx } from '../../../libs/clsx';
import { createGenericForwardRefsFactory } from '../../../libs/generic-forward-refs';
import { mergeProps } from '../../../libs/merge-props';
import { BaseColorName } from '../../../libs/theme';
import { Icon, IconColorProvider, MonochromeIconDefinition } from '../../icons';
import { Spacer } from '../../layout';
import { useTheme } from '../../theme-provider';
import { InverseThemeSemantics } from '../../theme-provider/inverse-theme-semantics';

import styles from './call-to-action.less';

interface CallToActionProps extends RefAttributes<HTMLAnchorElement | HTMLButtonElement> {
  outline?: boolean;
  disabled?: boolean;
  onPress?: (event: PressEvent) => void;
  size?: 'sm' | 'md' | 'lg';
  color?: 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'error';
  leadingIcon?: MonochromeIconDefinition;
  trailingIcon?: MonochromeIconDefinition;
  className?: string;
}

/**
 * Determines a _default_ color for icons nested in a `<CallToAction>`.
 */
function useIconColor(baseColor: CallToActionProps['color'], inverseSemantics?: boolean, outline?: boolean) {
  const theme = useTheme({ inverseSemantics });

  return useMemo(() => {
    let iconBaseColor: BaseColorName | undefined;

    switch (baseColor) {
      case 'primary':
        iconBaseColor = outline ? 'tertiary' : 'secondary';
        break;

      case 'secondary':
        iconBaseColor = outline ? 'secondary' : 'tertiary';
        break;

      case 'tertiary':
        iconBaseColor = outline ? 'tertiary' : 'secondary';
        break;

      case 'error':
        iconBaseColor = outline ? 'error' : 'secondary';
        break;

      case 'warning':
        iconBaseColor = outline ? 'warning' : 'secondary';
        break;

      case 'success':
        iconBaseColor = outline ? 'success' : 'secondary';
        break;

      default:
        iconBaseColor = undefined;
    }

    return iconBaseColor ? theme.hex[iconBaseColor].base : undefined;
  }, [theme.key, baseColor, outline]);
}

const createCallToActionComponents = /* @__PURE__ */ createGenericForwardRefsFactory(['button', 'a']);
const CallToActionComponents = /* @__PURE__ */ createCallToActionComponents<CallToActionProps>(
  'CallToAction',

  { color: 'primary', size: 'md' },

  (props, externalRef) => {
    const {
      children,
      onPress,
      className,
      disabled,
      outline,
      color,
      size,
      leadingIcon,
      trailingIcon,
      component,
      ...otherProps
    } = props;

    const { isFocusVisible, focusProps } = useFocusRing();
    const { pressProps, isPressed } = usePress({ onPress, isDisabled: disabled });
    const getScaleProps = transitions.useScaleIn();

    const inverseSemantics = (color === 'secondary' && outline) || (color === 'tertiary' && !outline);
    const iconColor = useIconColor(color, inverseSemantics, outline);

    const content = (
      <>
        <AnimatePresence>
          {isFocusVisible && <motion.span aria-hidden="true" className={styles.focusRing} {...getScaleProps(1.08)} />}
        </AnimatePresence>

        {leadingIcon && (
          <>
            <Icon size={size === 'sm' ? 20 : 24} type={leadingIcon} />
            <Spacer size={8} />
          </>
        )}

        <span className={styles.buttonLabel}>{children}</span>

        {trailingIcon && (
          <>
            <Spacer size={8} />
            <Icon size={size === 'sm' ? 20 : 24} type={trailingIcon} />
          </>
        )}
      </>
    );

    return (
      <IconColorProvider colorA={iconColor}>
        <InverseThemeSemantics active={inverseSemantics}>
          {React.createElement(
            component,

            {
              className: clsx([
                className,
                styles.CallToAction,
                styles[`size_${size}`],
                styles[`color_${color}`],
                disabled && styles.disabled,
                outline && styles.outline,
                isPressed && styles.active,
              ]),
              disabled,
              'aria-disabled': disabled,
              ...mergeProps(pressProps, focusProps, otherProps),
              ref: externalRef,
            },

            content,
          )}
        </InverseThemeSemantics>
      </IconColorProvider>
    );
  },
);

export const CallToAction = /* @__PURE__ */ Object.assign(CallToActionComponents.button, CallToActionComponents);
